import MescrollUni from "mescroll-uni/mescroll-uni.vue";
// 模拟数据
import mockData from "../../common/pdlist.js";

function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year;
	} else if(type=='7day'){
		day=day-7;
	} else if(type=='1month'){
		month=month-1;
	} else if(type=='3month'){
		month=month-3;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {
	components: {
		MescrollUni
	},
	data() {
		return {
				CustomBar: this.CustomBar,
				mescroll: null, //mescroll实例对象
				date1: getDate({
					format: true
				}),
				date2: getDate({
					format: true
				}),
				endDate:getDate('end'),
				agent_id:null,
				pageNum:1,
				pageSize:20,
				
				curPageData:[],
				totalPage:0,

			}
	},
	onLoad() {
		const agentInfo = uni.getStorageSync('agentInfo');
		if (agentInfo) {
			this.token=agentInfo.token;
		}
	},
	//注册滚动到底部的事件,用于上拉加载
	onReachBottom() {
		this.mescroll && this.mescroll.onReachBottom();
	},
	//注册列表滚动事件,用于下拉刷新
	onPageScroll(e) {
		this.mescroll && this.mescroll.onPageScroll(e);
	},
	methods: {
		navTo() {
			uni.redirectTo({
				url: '../tabbar/tabbar'
			});
		},
		ontoday(){
			let time2=getDate({format: true})
			let time1=getDate({format: true})
			this.date1 =time1;
			this.date2 =time2;
			this.loadlist();				
		},
		onseven(){
			let time2=getDate({format: true})
			let time1=getDate('7day');
			this.date1 =time1;
			this.date2 =time2;
			this.loadlist();
		},
		ononemonth(){
			let time2=getDate({format: true})
			let time1=getDate('1month');
			this.date1 =time1;
			this.date2 =time2;
			this.loadlist();
		},
		onthreemonth(){
			let time2=getDate({format: true})
			let time1=getDate('3month');
			this.date1 =time1;
			this.date2 =time2;
			this.loadlist();
		},
		loadlist(){
			uni.request({
			url: 'http://192.168.0.199:8080/agent/earnings/ajax-deposit-recod',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
					time1:this.date1+' 00:00:00',
					time2:this.date2+' 23:59:59',
					pageNum:this.pageNum,
					pageSize:this.pageSize,
				},
				success: res => {
					let lists = res;
					let data = lists.data
					if (data.code == 200) {
						// 接口返回的当前页数据列表 (数组)
						this.curPageData =data.data.list;
						for (let i=0;i < this.curPageData.length;i++) {
							let deposit_type=this.curPageData[i].deposit_type;
							if(deposit_type=="0"){
								this.curPageData[i].deposit_type="微信"
							}else if(deposit_type=="1"){
								this.curPageData[i].deposit_type="支付宝"
							}else if(deposit_type=="2"){
								this.curPageData[i].deposit_type="银行卡"
							}
							
							let status=this.curPageData[i].status;
							if(status=="0"){
								this.curPageData[i].status="待审核"
							}else if(status=="5"){
								this.curPageData[i].status="通过"
							}
							else if(status=="6"){
								this.curPageData[i].status="驳回"
							}
						}
						// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
						this.totalPage = data.data.totalPage; 
						this.mescroll.endByPage(this.curPageData.length, this.totalPage);
						if(this.mescroll.num == 1) this.dataList = []; //如果是第一页需手动置空列表
						
						this.dataList = this.dataList.concat(this.curPageData); //追加新数据
						if(this.dataList.length>0){
							this.mescroll.endSuccess()
						}
						
					} else {
						// 接口返回的当前页数据列表 (数组)
						this.curPageData =data.list;
						// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
						this.totalPage = data.totalPage; 
						this.mescroll.endByPage(this.curPageData.length, this.totalPage);
						if(this.mescroll.num == 1) this.dataList = []; //如果是第一页需手动置空列表
						this.dataList = this.dataList.concat(this.curPageData); //追加新数据
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
			});
		},
		DateChange1(e) {
			this.date1 = e.detail.value
		},
		DateChange2(e) {
			this.date2 = e.detail.value
		},
		changeSN(e){
			for (var i = 0; i < this.curPageData.length; ++i) {
				if (e === i) {
					this.curPageData[i].isDisplay = !this.curPageData[e].isDisplay;
				} else {
					this.curPageData[i].isDisplay = false;
				}
			}
		},
		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},
		// 下拉刷新的回调
		downCallback(mescroll){
			mescroll.resetUpScroll() // 重置列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认20 */
		upCallback(mescroll) {
			
			// 此时mescroll会携带page的参数:
				this.pageNum = mescroll.num; // 页码, 默认从1开始
				this.pageSize = mescroll.size; // 页长, 默认每页10条
			uni.request({
			url: 'http://192.168.0.199:8080/agent/earnings/ajax-deposit-recod',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
					time1:this.date1+' 00:00:00',
					time2:this.date2+' 23:59:59',
					pageNum:this.pageNum,
					pageSize:this.pageSize,
				},
				success: res => {
					let lists = res;
					let data = lists.data
					if (data.code == 200) {
						// 接口返回的当前页数据列表 (数组)
						this.curPageData =data.data.list;
						for (let i=0;i < this.curPageData.length;i++) {
							let playerList=this.curPageData[i].players;
							
							
							let profit_fee_ratio=this.curPageData[i].profit_fee_ratio*100
							this.curPageData[i].profit_fee_ratio=profit_fee_ratio+'%'
							
						}
						// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
						this.totalPage = data.data.totalPage; 
						this.mescroll.endByPage(this.curPageData.length, this.totalPage);
						if(this.mescroll.num == 1) this.dataList = []; //如果是第一页需手动置空列表
						
						this.dataList = this.dataList.concat(this.curPageData); //追加新数据
						if(this.dataList.length>0){
							this.mescroll.endSuccess()
						}
						
					} else {
						// 接口返回的当前页数据列表 (数组)
						this.curPageData =data.data.list;
						// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
						this.totalPage = data.data.totalPage; 
						this.mescroll.endByPage(this.curPageData.length, this.totalPage);
						if(this.mescroll.num == 1) this.dataList = []; //如果是第一页需手动置空列表
						this.dataList = this.dataList.concat(this.curPageData); //追加新数据
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
			});
				
		}
	}

}
